import { mapState, mapActions } from 'vuex'
import article from '@/components/article'
export default {
  onLoad(query) {
    this.clearMovieDetail()
    const { id } = query
    this.initPage(id)
  },
  components: {
    'v-article': article
  },
  computed: {
    ...mapState('movie', ['currentMovie'])
  },
  methods: {
    ...mapActions('movie', ['getMovieDetail', 'clearMovieDetail']),
    async initPage(id) {
      await this.getMovieDetail(id)
    }
  }
}
